@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap');

body {
  font-family: 'Oswald', sans-serif;
  overflow-x: hidden;
  overflow-y: hidden;
  user-select: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.nui-wrapper {
  display: flex;
  flex-direction: row;
  
  height: 100%;
}

.buttonContainer {

  display: flex;
  flex-direction: row;
  margin-top: auto;
  position: absolute;
  z-index: 2;
  bottom: 0;
  left: 35%;
  
  .otherOption {
    margin-bottom: 35px;
    margin-top: auto;
    margin-left: -20px;
  }
  
}

.toggleContainer {
  
  z-index: 2;
  
  display: flex;
  flex-direction: column;
  flex: 50%;

  margin-top: auto;
  margin-left: 40px;
  margin-bottom: 30px;

  h3 { text-shadow: 0px 0px 2px #000000; }

  .togglesRow {

    display: flex;
    flex-direction: row;

    .toggle {

      display: flex;
      flex-direction: column;
      text-transform: uppercase;

      margin-right: 16px;
      margin-top: -14px;

      font-family: Oswald;
      font-style: normal;
      font-weight: normal;
      font-size: 18px;

      color: #FFFFFF;
      text-shadow: 0px 0px 2px #000000;

      .option {

        
        margin-top: 5px;
        padding: 10px 12px; 
        background: rgba(255, 255, 255, 0.12);
        border: 1.5px solid rgba(255, 255, 255, 0.12);
        box-sizing: border-box;
        border-radius: 4px;

        .insideRect {
          width: 26px;
          height: 6px;

          background: #474D5D;
          border-radius: 4px;
        }

        .insideHex {
          position: absolute;
          bottom: 36px;
        }

      }

    }

  }

}

.mainMenu {

  display: flex;
  color: #FFFFFF;
  

  padding-bottom: 30px;

  .blocker.true {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 101;
  }

  .mainMenuBg {
    position: absolute;
    width: 100%;
    height: 100%;
    background: linear-gradient(89.87deg, rgba(43, 57, 98, 0.01) 0.12%, rgba(39, 48, 80, 0.75) 13.39%, #24273F 99.88%);
    z-index: 1;
  }

  .menu {
    margin-top: 32px;
    margin-right: 12px;

    padding-right: 40px;
    overflow: auto!important;
    z-index: 50;

    .somefuckingdogshit {
      text-transform: uppercase;
      font-family: Oswald;
      font-style: normal;
      font-weight: normal;
      font-size: 46px;
      text-align: right; 

      span {
        display: block;
        margin-bottom: 40px;
        font-size: 18px;
        color:rgba(255, 255, 255, 0.5)
      }
      
    }

    .menuOptions {
      
      z-index: 10;
      
      
      display: flex;
      flex-direction: column;
      align-items: flex-end;

      margin-left: 115px;

      .parentOption {
        margin-top: 24px;
      }

      span {
        font-family: Oswald;
        font-style: normal;
        font-weight: normal;
        font-size: 28px;
        color: #FFFFFF;
      }

    }

  }

}

.design {

  overflow: hidden;
  
  z-index: 50;
  
  .one {
    position: absolute;
    right: 0px;
    bottom: 0px;
  }

  .two {
    position: absolute;
    right: 510px;
    bottom: 0px;
  }

  .three {
    position: absolute;
    right: 475px;
  }

  .four {
    position: absolute;
    right: 0;
  }

  .five {
    position: absolute;
    right: 0;
    bottom: 0;
  }

}

h3 {
  font-family: Oswald;
  font-style: normal;
  font-weight: normal;
  font-size: 28px;
  color: #FFFFFF;
}

.optionGroup {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

  justify-content: space-between;

  width: 570px;

}

.optionDiv {
  display: flex;
  flex-direction: column;

  margin-top: 24px;

  &.bumpUp {
    margin-top: 3px;
  }

  &.bumpDown {
    margin-top: 33px;
  }

  .optionText {
    font-family: Oswald;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 96.2%;
    /* or 17px */
    
    margin-bottom: 12px;

    text-transform: capitalize;
    
    color: #FFFFFF;
    
    opacity: 0.8;
  }

  .leftRight {

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    width: 250px;

    overscroll-behavior: contain;

    &.long {
      width: 570px;
    }

    &.medium {
      width: 503.5px;
    }

    padding: 14px 21px;

    background: rgba(255, 255, 255, 0.12);
    border: 1.5px solid rgba(255, 255, 255, 0.12);
    box-sizing: border-box;
    border-radius: 4px;

    .optionNumber {
      all: unset;
      font-family: Oswald;
      font-size: 24px;
      line-height: 96.2%;
      text-align: center;
      color: #FFFFFF;
    }

  }

  .leftRightScroll {

    &.long {
      width: 529px;
    }

    display: flex;
    justify-content: center;
    align-items: center;

    width: 250px;
    height: 30px;

    background: rgba(255, 255, 255, 0.12);
    border: 1.5px solid rgba(255, 255, 255, 0.12);
    box-sizing: border-box;
    border-radius: 4px;

    .backdrop {
      
      &.long {
        width: 496px;
      }

      width: 226px;

      

      height: 6px;
      border-radius: 4px;

      input[type='range'] {

        user-select: auto;
        -webkit-appearance: none;
        background: rgba(255, 255, 255, 0.16);
        position: relative;
        bottom: 13px;
        border-radius: 4px;
        width: 225px;
        height: 6px;
        
      }
      
      input[type='range']::-webkit-slider-runnable-track {
        -webkit-appearance: none;
        user-select: auto;
        height: 20px;
      }
      
      input[type='range']::-webkit-slider-thumb {
        -webkit-appearance: none;
        user-select: auto;
        cursor: pointer;
        width: 20px;
        height: 22px;
        background-image: url("images/Polygon114.svg");
      }

      .progressHex {
        position: relative;
        bottom: 33px;
        left: 94%; /* -6% */
        user-select: none;
        pointer-events: none;
        z-index: 1;
      }

    }

    .progress {
      width: 100%;
      height: 6px;
      background: linear-gradient(90deg, #4E65A7 -1.57%, #56FDFD 102.99%);
      border-radius: 4px;
    }

  }

}

.removeTat {
  display: flex;
  justify-content: center;
  align-items: center;

  padding: 0px 15.25px;
  color: #57F9FA;

  background: rgba(255, 255, 255, 0.12);
  border: 1.5px solid rgba(255, 255, 255, 0.12);
  box-sizing: border-box;
  border-radius: 4px;

  margin-left: 16px;
}

.flexRow {
  display: flex;
  flex-direction: row;
  margin-bottom: 12px;
}

.addTat {
  display: flex;
  justify-content: center;
  align-items: center;

  background: rgba(255, 255, 255, 0.12);
  border: 1.5px solid rgba(255, 255, 255, 0.12);
  box-sizing: border-box;
  border-radius: 4px;

  font-family: Oswald;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 96.2%;
  /* or 15px */
  
  text-align: center;
  
  color: #FFFFFF;
  
  padding: 8px;

  width: 41px;

  margin-top: 24px;
}

.rightContainer {

  display: flex;
  flex-direction: column;

  height: 100%;
  background: rgba(255, 255, 255, 0.1);

  padding-top: 32px;
  padding-left: 24px;
  padding-right: 24px;

  z-index: 51;
  
  svg {

    margin-top: 20px;

    z-index: 52;

    &.active {

      .before {
        display: none;
      }
  
      .active {
        display:block;
      }

    }

    stroke: url(#paint0_linear_302_442);
    stroke-opacity: 0.45;
    stroke-width: 2;

    .active {
      display: none;
    }

  }

}



.fade-enter {
  opacity: 0;
}
.fade-enter-active {
  opacity: 1;
  transition: opacity 200ms;
}
.fade-exit {
  opacity: 1;
}
.fade-exit-active {
  opacity: 0;
  transition: opacity 200ms;
}

/* width */
::-webkit-scrollbar {
  width: 3px;
}

/* Track */
::-webkit-scrollbar-track {
  background: none;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 3px;
}

.colorPicker {
  position: absolute;
  right: 50px;
  top: 50px;

  display: flex;
  flex-direction: column;

  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(117, 117, 117, 0.4);
  box-sizing: border-box;
  box-shadow: 0px 2px 21px rgba(0, 0, 0, 0.37);
  backdrop-filter: blur(28px);

  width: 411px;
  
  z-index: 500;

  .header {
    display: flex;
    padding: 10px 24px;
    align-items: center;

    font-family: Oswald;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
    /* identical to box height */


    color: #FFFFFF;

  }
  
  .options {
    background: rgba(255, 255, 255, 0.05);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    padding: 20px 24px 24px 24px;

    display: flex;
    flex-wrap: wrap;
    
    .hexagon {
      margin-left: 5px;
      margin-top: 5px;
      
    }
  }

}

.fadeWee-enter {
  opacity: 0;
  transform: scale(0.9);
}
.fadeWee-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 300ms, transform 300ms;
}
.fadeWee-exit {
  opacity: 1;
}
.fadeWee-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 300ms, transform 300ms;
}